.child-page-warp {
  height: 10rem;
  width: 100%;
}
.child-page-item {
  width: 21.25rem;
  border-bottom: 2px solid #f6f7fb;
  padding: 1rem 0px;
  margin-left: calc(100% - 21.25rem);
  border-bottom: 1px solid #ccc;
}

.child-page-top img {
  width: 7rem;
}
.child-page-title {
  height: 1.34rem;
  width: 100%;
  font-weight: 400;
  font-size: 1rem;
}
.child-page-top-right {
  margin-left: 0.78rem;
  flex: 1 1 0;
  margin-right: 0.4rem;
}
.child-page-top {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
}
.child-page-type-list {
  display: flex;
  justify-content: flex-start;
}
.child-page-type {
  border: 1px solid #ccc;
  opacity: 0.4;
  padding: 0rem 0.7rem;
  font-size: 0.8rem;
  text-align: center;
  margin: 0.3rem 0px;
  margin-right: 0.4rem;
  border-radius: 3px;
}
.child-page-message {
  font-size: 0.79rem;
  opacity: 0.4;
  text-overflow: -o-ellipsis-lastline;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}
.child-page-bottom {
  margin-top: 0.4rem;
}
.child-page-bottom div {
  font-size: 0.9rem;
  opacity: 0.5;
  margin-top: 0.1rem;
}
